export default {
	data() {
		return {
			info: {
				uid: '',
				name: '',
				gender: '',
				birthDay: '',
				avatar: '',
				breed: '',
				state: ''
			},
			gender: '',
			genderArray: [{
				id: 1,
				name: '雄',
			}, {
				id: 2,
				name: '雌'
			}],
			stateArray: [
				'已绝育', '未绝育'
			],
			api: this.$API
		}
	},
	onShow(options) {
		let page = getCurrentPages();  
		this.info.breed = page[1].$vm.breed;
	},
	methods: {
		addPet() {
			if (this.info.name !== '' && this.info.gender !== '' && this.info.birthDay !== '' && this.info.avatar !== '' &&
				this.info.breed !== '' && this.info.state !== '') {
				this.$http.post('/petbnb/addPet', {
						...this.info
					})
					.then(res => {
						if (res.code === 1) {
							uni.showToast({
								title: res.message,
								icon: 'success'
							})
							setTimeout(()=>{
								uni.navigateBack({
									delta: 1
								})
							},1000)
						}
					})
					.catch(err => {

					})
			} else {
				uni.showToast({
					title: '请将信息填写完整',
					icon: 'none'
				})
			}
		},
		// 宠物头像
		changeAvatar() {
			let that = this;
			uni.chooseImage({
				count: 1,
				success: res => {
					this.$http.uploadFile(res.tempFilePaths[0]).then(res => {
							that.info.avatar = res.data.path;
						})
						.catch(err => {
							uni.showToast({
								icon: 'none',
								title: '系统错误，请稍后再试!'
							})
						})
				}
			})
		},
		// 宠物姓名
		petName(e) {
			this.info.name = e.detail.value;
		},
		// 宠物性别
		petGender(e) {
			this.gender = this.genderArray[e.detail.value].name;
			this.info.gender = this.genderArray[e.detail.value].id;
		},
		// 宠物品种
		petBreed() {
			uni.navigateTo({
				url: '../encyclopedia/encyclopedia'
			})
		},
		// 宠物生日
		petBirthDay(e) {
			this.info.birthDay = e.detail.value;
		},
		// 宠物绝育状态
		petState(e) {
			this.info.state = this.stateArray[e.detail.value];
		}
	}
}
